<template>
  <div class="jumbotron">
    <h1>All Jobs for user: {{username}}</h1>
    <div class="card-deck" v-for="jobset in jobs" :key="jobset._DAG">
      <div class="card mb-3 w-25" v-for="job in jobset" :key="job.DAG">
        <h3 class="card-header">Job ID {{job.DAG}}</h3>
        <div class="card-body">
          <h5 class="card-title">{{job.Total}} Total Jobs</h5>
          <h6 class="card-subtitle text-muted">Started at {{job.Time}} on {{job.Date}}</h6>
          <router-link :to="job.DAG" class="stretched-link"></router-link>
        </div>
        <div class="card-footer text-muted">
          <div class="progress">
            <div
              class="progress-bar"
              role="progressbar"
              :style="job.percent"
              aria-valuenow="5"
              aria-valuemin="0"
              aria-valuemax="100"
            ></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const API_URL = "http://localhost:8000/api";
export default {
  name: "HomePage",
  data: () => ({
    jobs: [[]],
    username: ''
  }),
  mounted() {
    this.load();
  },
  methods: {
    async load() {
      let response = await fetch(API_URL);
      if (response.status === 200) {
        const data = await response.json();
        let allJobs = [];
        let batch = [];
        data.jobs.sort(
          (a, b) => (100 * b.Done) / b.Total - (100 * a.Done) / a.Total
        );
        for (let i = 0; i < data.jobs.length; i++) {
          batch.push({
            ...data.jobs[i],
            DAG: data.jobs[i].DAG.replace("DAG+", ""),
            percent:
              "width: " + (100 * data.jobs[i].Done) / data.jobs[i].Total + "%;"
          });
          if ((i + 1) % 4 === 0) {
            allJobs.push(batch);
            batch = [];
          }
        }
        this.jobs = allJobs;
        this.username = allJobs[0][0].Username
      }
    }
  }
};
</script>

